.about-section {
    width: 100%;
    min-height: 100vh;
    padding: 50px 20px;
    background-color: white;
}

.about-container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.about-container h1 {
    font-size: 3rem;
    color: blue;
    margin-bottom: 40px;
    position: relative;
    display: inline-block;
}

.about-container h1::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 4px;
    background-color: paleturquoise;
}

.about-content {
    text-align: left;
    max-width: 800px;
    margin: 0 auto;
}

.about-content p {
    font-size: 1.1rem;
    line-height: 1.8;
    margin-bottom: 20px;
    color: #333;
}

.education {
    margin-top: 40px;
}

.education h3 {
    font-size: 1.5rem;
    color: blue;
    margin-bottom: 20px;
}

.education ul {
    list-style-type: none;
}

.education li {
    font-size: 1.1rem;
    margin-bottom: 10px;
    padding-left: 20px;
    position: relative;
}

.education li::before {
    content: '▹';
    position: absolute;
    left: 0;
    color: blue;
}

@media (max-width: 768px) {
    .about-container h1 {
        font-size: 2.5rem;
    }
    
    .about-content p {
        font-size: 1rem;
    }
}