.home-section {
    width: 100%;
    min-height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: paleturquoise;
    padding: 20px;
}

.main-container {
    width: 100%;
    max-width: 1200px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.image {
    width: 400px;
    height: auto;
    overflow: hidden;
    box-shadow: 0 0 50px rgba(175, 238, 238, 0.7);
    margin: 20px;
    border-radius: 10px;
}

.image img {
    width: 100%;
    height: auto;
    display: block;
    border-radius: 10px;
}

.image:hover {
    animation: animate 1.5s ease-in-out infinite;
}

@keyframes animate {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}

.content {
    color: black;
    width: 40%;
    min-width: 300px;
    margin: 20px;
}

.content h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
}

.content h1 span {
    color: blue;
    text-shadow: 0 0 10px blue;
}

.typewriter {
    font-size: 1.8rem;
    font-weight: 600;
    margin-bottom: 15px;
}

.typewrite-text {
    color: blue;
    text-shadow: 0 0 10px blue;
}

.content p {
    font-size: 1rem;
    margin: 20px 0;
    line-height: 1.6;
}

.social-links {
    display: flex;
    margin: 20px 0;
}

.social-links a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    background-color: transparent;
    border: 0.2rem solid blue;
    border-radius: 50%;
    color: blue;
    margin: 0 15px;
    font-size: 1.5rem;
    transition: 0.2s linear;
    text-decoration: none;
}

.social-links a:hover {
    transform: scale(1.3);
    color: paleturquoise;
    background-color: blue;
    filter: drop-shadow(0 0 10px blue);
}

.btn a {
    display: inline-block;
    width: 50%;
    padding: 15px;
    background-color: blue;
    color: white;
    border: none;
    outline: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 5px;
    transition: 0.2s linear;
    text-align: center;
    text-decoration: none;
}

.btn a:hover {
    transform: scale(1.1);
    color: blue;
    border: 2px solid blue;
    background-color: transparent;
    font-weight: 700;
    box-shadow: 0 0 40px blue;
}

@media (max-width: 768px) {
    .main-container {
        flex-direction: column;
    }
    
    .content {
        width: 80%;
        text-align: center;
    }
    
    .social-links {
        justify-content: center;
    }
    
    .btn a {
        width: 80%;
        margin: 0 auto;
    }
}

@media (max-width: 480px) {
    .content h1 {
        font-size: 2rem;
    }
    
    .typewriter {
        font-size: 1.5rem;
    }
    
    .social-links a {
        width: 2.5rem;
        height: 2.5rem;
        font-size: 1.2rem;
    }
    
    .btn a {
        width: 100%;
    }
}