.skills-section {
    width: 100%;
    min-height: 100vh;
    padding: 50px 20px;
    background-color: paleturquoise;
}

.skills-container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.skills-container h1 {
    font-size: 3rem;
    color: blue;
    margin-bottom: 40px;
    position: relative;
    display: inline-block;
}

.skills-container h1::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 4px;
    background-color: blue;
}

.skills-content {
    max-width: 800px;
    margin: 0 auto;
}

.skills-grid {
    margin-bottom: 40px;
}

.skill-item {
    margin-bottom: 20px;
}

.skill-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

.skill-info span {
    font-size: 1.1rem;
    color: #333;
}

.skill-bar {
    width: 100%;
    height: 10px;
    background-color: #ddd;
    border-radius: 5px;
    overflow: hidden;
}

.skill-progress {
    height: 100%;
    background-color: blue;
    border-radius: 5px;
    transition: width 1s ease-in-out;
}

.experience {
    text-align: left;
}

.experience h2 {
    font-size: 1.8rem;
    color: blue;
    margin-bottom: 20px;
}

.experience p {
    font-size: 1.1rem;
    line-height: 1.8;
    color: #333;
}

@media (max-width: 768px) {
    .skills-container h1 {
        font-size: 2.5rem;
    }
    
    .skill-info span {
        font-size: 1rem;
    }
    
    .experience h2 {
        font-size: 1.5rem;
    }
    
    .experience p {
        font-size: 1rem;
    }
}