.nav-container {
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: paleturquoise;
    z-index: 1000;
}

.logo {
    color: blue;
    font-size: 2rem;
    font-weight: bold;
    margin-top: 80px;
}

.logo span {
    color: blanchedalmond;
    text-shadow: 0 0 10px blanchedalmond;
}

.hamburg, .close-icon {
    cursor: pointer;
    color: blue;
    display: none;
    font-size: 1.5rem;
}

.links {
    display: flex;
}

.links a {
    position: relative;
    font-size: 1.2rem;
    color: blue;
    margin: 0 20px;
    text-decoration: none;
    font-weight: 550;
    transition: 0.3s linear;
}

.links a::before {
    position: absolute;
    content: "";
    bottom: -3px;
    left: 0;
    width: 0%;
    height: 3px;
    background-color: antiquewhite;
    transition: 0.2s linear;
}

.links a:hover::before {
    width: 100%;
}

.links a:hover {
    color: antiquewhite;
}

@media (max-width: 768px) {
    .hamburg, .close-icon {
        display: block;
    }
    
    .links {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.9);
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transform: translateY(-100%);
        transition: transform 0.3s ease-in-out;
    }
    
    .links.active {
        transform: translateY(0);
    }
    
    .links a {
        margin: 15px 0;
        font-size: 1.5rem;
    }
    
    .close-icon {
        position: absolute;
        top: 20px;
        right: 20px;
    }
}