.footer {
    width: 100%;
    background-color: #333;
    color: white;
    padding: 50px 20px 20px;
}

.footer-container {
    max-width: 1200px;
    margin: 0 auto;
}

.footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
}

.footer-about,
.footer-links,
.footer-social {
    flex: 1;
    min-width: 250px;
    margin-bottom: 30px;
}

.footer-about h3,
.footer-links h3,
.footer-social h3 {
    font-size: 1.5rem;
    margin-bottom: 20px;
    color: paleturquoise;
}

.footer-about p {
    font-size: 1rem;
    line-height: 1.6;
    color: #ddd;
}

.footer-links ul {
    list-style: none;
}

.footer-links li {
    margin-bottom: 10px;
}

.footer-links a {
    color: #ddd;
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-links a:hover {
    color: paleturquoise;
}

.social-icons {
    display: flex;
    gap: 15px;
}

.social-icons a {
    color: white;
    font-size: 1.5rem;
    transition: color 0.3s ease;
}

.social-icons a:hover {
    color: paleturquoise;
}

.footer-bottom {
    text-align: center;
    padding-top: 20px;
    border-top: 1px solid #444;
}

.footer-bottom p {
    font-size: 0.9rem;
    color: #aaa;
}

@media (max-width: 768px) {
    .footer-content {
        flex-direction: column;
    }
    
    .footer-about,
    .footer-links,
    .footer-social {
        margin-bottom: 30px;
    }
}