.contacts-section {
    width: 100%;
    min-height: 100vh;
    padding: 50px 20px;
    background-color: paleturquoise;
}

.contacts-container {
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.contacts-container h1 {
    font-size: 3rem;
    color: blue;
    margin-bottom: 40px;
    position: relative;
    display: inline-block;
}

.contacts-container h1::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 80px;
    height: 4px;
    background-color: blue;
}

.contacts-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 40px;
}

.contact-info {
    flex: 1;
    min-width: 300px;
    margin-right: 30px;
    text-align: left;
}

.contact-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 30px;
}

.contact-icon {
    font-size: 1.5rem;
    color: blue;
    margin-right: 20px;
    margin-top: 5px;
}

.contact-text h3 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 5px;
}

.contact-text p {
    font-size: 1.1rem;
    color: #555;
}

.contact-form {
    flex: 1;
    min-width: 300px;
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: left;
}

.contact-form h2 {
    font-size: 1.8rem;
    color: blue;
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 20px;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1rem;
    transition: border 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: blue;
}

.form-group textarea {
    resize: vertical;
}

.contact-form button {
    background-color: blue;
    color: white;
    border: none;
    padding: 12px 30px;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.contact-form button:hover {
    background-color: #0056b3;
}

@media (max-width: 768px) {
    .contacts-container h1 {
        font-size: 2.5rem;
    }
    
    .contacts-content {
        flex-direction: column;
    }
    
    .contact-info {
        margin-right: 0;
        margin-bottom: 30px;
    }
    
    .contact-text h3 {
        font-size: 1.3rem;
    }
    
    .contact-text p {
        font-size: 1rem;
    }
}