.install-prompt {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    padding: 15px;
    z-index: 1000;
    width: 90%;
    max-width: 400px;
    animation: slideUp 0.3s ease-out;
  }
  
  .install-prompt-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .install-prompt p {
    margin-bottom: 15px;
    font-size: 1rem;
    color: #333;
    text-align: center;
  }
  
  .install-prompt-buttons {
    display: flex;
    gap: 10px;
    width: 100%;
  }
  
  .install-prompt-buttons button {
    flex: 1;
    padding: 8px 12px;
    border-radius: 5px;
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .install-btn {
    background-color: blue;
    color: white;
    border: none;
  }
  
  .install-btn:hover {
    background-color: #0056b3;
  }
  
  .dismiss-btn {
    background-color: #f0f0f0;
    color: #333;
    border: 1px solid #ddd;
  }
  
  .dismiss-btn:hover {
    background-color: #e0e0e0;
  }
  
  @keyframes slideUp {
    from {
      transform: translateX(-50%) translateY(100%);
      opacity: 0;
    }
    to {
      transform: translateX(-50%) translateY(0);
      opacity: 1;
    }
  }